button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
		border: none;
		@include background;
		color: $light_gray;
		height: 45px;
		width: 150px;
		font-size: 13px;
		letter-spacing: 0.1rem;
		font-family: $font__main;
		opacity:1;
		text-align: center;
		cursor: pointer;
    	border-radius: 2px;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	@media screen and (min-width: $large_screen) {
		box-shadow: 0 1px 7px rgba(0, 0, 0, 0.7);
	}
}

.hide-on-small-screen {
    display: none;
    @media screen and (min-width: $medium_screen) {
        display: block;
    } 
}

.middle {
    width: 150px;
    margin: 0 auto;
}



@mixin transition-read-more {
    transition: all 0.2s ease-in;  
}

.red_link,
.less,
.red_link--more {
	display: grid;
	place-items: center;
	border: 1px solid $red_main;
	background-color:rgba(0, 0,0, 0);
	border-radius: 5px;
	height: 45px;
	width: 150px;
	font-size: 13px;
	letter-spacing: 0.1rem;
	font-family: $font__main;
	text-align: center;
	cursor: pointer;
	border-radius: 2px;
	margin-top: 1rem;
	@media screen and (min-width: $large_screen) {
		@include transition-read-more;
		will-change: background, color, opacity;
	}	
}

.red_link:visited,
.less:visited,
.red_link--more:visited {
		color: $red_main;
}

.red_link:hover,
.less:hover,
.red_link--more:hover {
	background-color:$red_main;
	color: $light_gray;
}
	


.red_link--more {
	display: none;
	@media screen and (min-width: $medium_large_screen) {
		display: grid;
		place-items: center;
		@include transition-read-more;
	}
}

.start {
	opacity:1;
	height: 100%;
	transition: all 0.2s ease-in;
}

.hide-a {
	@include transition-read-more;
	display:none;
	margin:0;
}

.hide {
	z-index: -100;
	height: 0;
	opacity:0;
	transition: all 0.2s ease-in;
}

.yellow-a {
	border:1px solid antiquewhite;
	background: antiquewhite;
	color: $black_color;
}

.white-text:visited{
	color: white;
}

